
.masonry-area {
  background: $dark;
  position: relative;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  overflow: hidden;
  text-overflow: ellipsis;
  @include shadow-8dp();

  .masonry-area-hover-text-header {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    padding: 1rem 1.25rem 1rem;
    opacity: 0;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    color: $light;
  }
  .masonry-area-hover-center {
    position: absolute;
    width: 100%;
    top: 30%;
    opacity: 0;
    padding-left: 1.25rem;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    text-align: center;

  }
  &:hover {
    //margin-top: -5px;
    -webkit-box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
    .masonry-area-hover-text-header, .masonry-area-hover-center {
      opacity: 1;
    }
    .masonry-area-image {
      &.with-hover-overlay:before {
        opacity: 0.7;
      }
    }
  }
  h2:last-child, p:last-child {
    margin-bottom: 0;
  }
  .masonry-area-image {
    display: block;
    position: relative;
    //min-height: 6.25rem;
    background: $card-bg;
    &.with-hover-overlay:before {
      display: block;
      position: absolute;
      content: " ";
      background: $dark;
      opacity: 0;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
    }
    &:hover {
      text-decoration: none;
    }
  }
}
