.masonry-area {
  background: #070707;
  position: relative;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  overflow: hidden;
  text-overflow: ellipsis;
  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
}

.masonry-area .masonry-area-hover-text-header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  padding: 1rem 1.25rem 1rem;
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  color: #f8f9fa;
}

.masonry-area .masonry-area-hover-center {
  position: absolute;
  width: 100%;
  top: 30%;
  opacity: 0;
  padding-left: 1.25rem;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  text-align: center;
}

.masonry-area:hover {
  -webkit-box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
}

.masonry-area:hover .masonry-area-hover-text-header, .masonry-area:hover .masonry-area-hover-center {
  opacity: 1;
}

.masonry-area:hover .masonry-area-image.with-hover-overlay:before {
  opacity: 0.7;
}

.masonry-area h2:last-child, .masonry-area p:last-child {
  margin-bottom: 0;
}

.masonry-area .masonry-area-image {
  display: block;
  position: relative;
  background: #fff;
}

.masonry-area .masonry-area-image.with-hover-overlay:before {
  display: block;
  position: absolute;
  content: " ";
  background: #070707;
  opacity: 0;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.masonry-area .masonry-area-image:hover {
  text-decoration: none;
}

