@mixin input-background-image($color) {
    background-image: -webkit-linear-gradient($color, $color), -webkit-linear-gradient(#D2D2D2, #D2D2D2);
    background-image: -o-linear-gradient($color, $color), -o-linear-gradient(#D2D2D2, #D2D2D2);
    background-image: linear-gradient($color, $color), linear-gradient(#D2D2D2, #D2D2D2);

    &:focus + &-placeholder {
      color: $color;
    }
}

@mixin card-media-aspect-ratio($x, $y) {
  &::before {
    // This clever trick brought to you by: http://www.mademyday.de/css-height-equals-width-with-pure-css.html
    margin-top: percentage($y / $x);
  }
}

@mixin card-container-layout {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}


// Make gradient
@mixin make-gradient($name, $value) {
  .#{$name}-gradient {
    background: linear-gradient(40deg, map-get($value, start), map-get($value, end)) !important;
  }
}

$opacity: .9;

// Make gradient
@mixin make-gradient-rgba($name, $value) {
  .#{$name}-gradient-rgba {
    background: linear-gradient(40deg, map-get($value, start), map-get($value, end)) !important;
  }
}

// Make gradient button
@mixin make-gradient-button($name, $value) {
  .btn {
    &.#{$name}-gradient {
      transition: .5s ease;
      color: $white-base;
      &:hover,
      &:focus,
      &:active,
      &:active:focus &.active {
        background: linear-gradient(lighten(map-get($value, start), 5%), lighten(map-get($value, end), 5%));
      }
    }
  }
}
