$theme-colors: (
  primary: $blue2,
  secondary: $gray-600,
  success: $green,
  info: $blue,
  warning: $yellow,
  danger: $red,
  purple:$purple,
  indigo: $indigo,
  yellow_neon: #EBEA6B,
  softgreen: #92CE92,
  pink: $pink,
  softpink: #E33D7E,
  softpurple: #725991,
  orange: $orange,
  light: $gray-100,
  grey: $grey,
  dark: $dark,
  dark-blue: #0d47a1,
  darkest-bluegrey: #1f2251
);

$gradient-peach:      linear-gradient(40deg, #FFD86F, #FC6262);
$gradient-aqua:       linear-gradient(40deg, #3aa2ff, #1fffac);
$gradient-purple:     linear-gradient(40deg, #ff88ce, #8f8bf7);
$gradient-blue:       linear-gradient(40deg, #5ed1fc, #3647b3);


$enable-rounded:            true;
$enable-shadows:            true;
$enable-gradients:          true;
$enable-transitions:        true;
$enable-hover-media-query:  true;
$enable-grid-classes:       true;
$enable-print-styles:       true;

$border-radius:          0.125rem;
$border-radius-lg:       0.625rem;
//$border-radius-sm:       0.125rem;
$border-radius-flat:     0;


$font-weight-custom: 600;
//$font-weight:;
$font-size-custom: .75rem;
$font-family-sans-serif:    Roboto, "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;
$font-family-base:          $font-family-sans-serif;

$navbar-padding-y:          0.75rem;
$navbar-padding-x:          1rem;

$input-btn-padding-y:       .75rem;
$input-btn-padding-x:       1.875rem;

$input-btn-padding-y-sm:    0.3125rem;
$input-btn-padding-x-sm:    1.25rem;

$input-btn-padding-y-lg:    1.125rem;
$input-btn-padding-x-lg:    2.375rem;

//$btn-font-weight:                $font-weight-custom;
$btn-font-size:                  .83rem;
$btn-rounded-border:             10em;
$btn-border-radius:              $border-radius;
$btn-border-radius-lg:           $border-radius;
$btn-border-radius-sm:           $border-radius;
$btn-transition: all 0.15s ease-in-out;
//$btn-border-radius-flat:         $border-radius-flat;

// Buttons
//$btn-color-basic:             $white-base !default;
$btn-margin:            .375rem;
$btn-padding-y:         .84rem;
$btn-padding-x:         2.14rem;

$btn-padding-y-lg:         1rem;
$btn-padding-x-lg:         2.4rem;
$btn-font-size-lg:         .94rem;
/*
$btn-padding-y-medium:        .7rem !default;
$btn-padding-x-medium:        1.6rem !default;
$btn-font-size-medium:        .7rem !default;
*/
$btn-padding-y-sm:         .5rem;
$btn-padding-x-sm:         1.6rem;
$btn-font-size-sm:         .64rem;

$btn-outline-padding-y:         .7rem;
$btn-outline-padding-y-lg:         .88rem;
//$btn-outline-padding-y-medium:        .58rem !default;
$btn-outline-padding-y-sm:         .38rem;



$input-padding-x:                0;//.75rem;
$input-padding-y:                .5rem;

$input-padding-x-sm:             0;//.5rem;//8px
$input-padding-y-sm:             .25rem;//4px

$input-padding-x-lg:             0;//1.5rem;//24px
$input-padding-y-lg:             .75rem;//12px

$input-line-height:              1.25;

$input-transition:              background 0s ease-out;

$input-btn-border-width:         0; // For form controls and buttons
$input-box-shadow:               none;



$alert-border-radius:         $border-radius-flat;
/*$checkbox-animation-check: 0.3s;
$checkbox-checked-color: $blue;
$checkbox-transform-scale: 2.3, 2.3, 1;
$checkbox-animation-ripple: 500ms;
*/

$alert-padding-y:             1.25rem;
$alert-padding-x:             1.25rem;
$alert-font-size:             0.875rem;

$shadow-key-penumbra-opacity:     0.14;
$shadow-key-umbra-opacity:        0.2;
$shadow-ambient-shadow-opacity:   0.12;

$badge-font-size:             75%;
$badge-font-weight:           $font-weight-custom;
$badge-padding-y:             .5em;
$badge-padding-x:             .75em;

$badge-pill-padding-x:        .75em;
// Use a higher than normal value to ensure completely rounded edges when
// customizing padding or font-size on labels.
$badge-pill-border-radius:    10rem;

$tooltip-padding-y:           .5rem;
$tooltip-padding-x:           .75rem;
$tooltip-color:               $white;
$tooltip-bg:                  rgba(#616161, .9);
$tooltip-arrow-color:         $tooltip-bg;

$progress-height:               .25rem;

// Masks
$mask-overlay-transition:     all .4s ease-in-out !default;
$mask-zoom-transition:        all .2s linear !default;
$mask-zoom-transform:         scale(1.1) !default;
$intro-video-transform:       translateX(-50%) translateY(-50%) !default;
$intro-video-transition:      1s !default;

// Images
$image-path: "../img" !default;

// Shadows
$z-depth-1:          0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12) !default;
$z-depth-1-half:     0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15) !default;
$z-depth-2:          0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19) !default;
$z-depth-3:          0 12px 15px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19) !default;
$z-depth-4:          0 16px 28px 0 rgba(0, 0, 0, 0.22), 0 25px 55px 0 rgba(0, 0, 0, 0.21) !default;
$z-depth-5:          0 27px 24px 0 rgba(0, 0, 0, 0.2), 0 40px 77px 0 rgba(0, 0, 0, 0.22) !default;

// Transitions
$transition-basic:            all .2s ease-in-out !default;
$transition-hoverable:        all .55s ease-in-out !default;
